import React from "react";

import Argument from "./Argument";
import Board from "./Board";
import Back from "./Back";

class Question extends React.Component {
  render() {
    const zone = this.props.passed_data.data
      .filter((item) => item.zone === this.props.number)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));

    const selectOptions = this.props.options.map((i) => (
      <option value={i}>{i}</option>
    ));
    return (
      <div>
        <Back func={() => document.getElementById("Home").click()} />
        <h3>{this.props.questionText}</h3>
        <Board
          handleDrop={this.props.handleDrop}
          identity={this.props.number}
          fill={zone}
        ></Board>
        <select
          value={this.props.val}
          onChange={this.props.handleQuestionChange}
          name={this.props.selectName}
          disabled={
            this.props.qsub
              ? true
              : zone.length === 0
              ? true
              : false
          }
          style={{ fontSize: "18px", fontFamily: "Geneva" }}
        >
          <option value="">-- Please Choose a condition --</option>
          {selectOptions}
        </select>

        <button
          onClick={this.props.passed_data.qanswers[this.props.number-101] === "" ? null : this.props.ok}
          disabled={
            this.props.passed_data.qanswers[this.props.number-101] === ""
              ? true
              : this.props.passed_data.qsubmitted[this.props.number-101]
              ? true
              : false
          }
          style={{ fontSize: "18px", fontFamily: "Geneva" }}
        >
          Submit
        </button>
        {this.props.passed_data.q1submitted && (
          <button
            style={{
              fontSize: "18px",
              fontFamily: "Geneva",
              marginLeft: "5px"
            }}
            onClick={() => document.getElementById("Home").click()}
          >
            Continue
          </button>
        )}
      </div>
    );
  }
}
export default Question;
