import React from "react";
import Argument from "./Argument";

import background from "./pics/paper.jpg";
// import bigbackground from "./pics/big99background.png"

import cdiag from "./pics/cdiag-removebg-preview.png";
import cbody from "./pics/cbody-removebg-preview.png";
// import cblood from "./pics/cblood-removebg-preview.png"
// import cbrain from "./pics/cbrainimcal-removebg-preview.png"
import ccomputer from "./pics/ccomputer-removebg-preview.png";
import cpostit from "./pics/cpostit-removebg-preview.png";

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      props: props
    };
  }

  handleDragEnter = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };
  handleDragLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };
  handleDragOver = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };

  render() {
    const noteb = this.props.identity === 99 ? "Notebook" : null;
    var elongate = 0;
    const normalStyle = {
      height: elongate > 5 ? "900px" : "450px",
      width: "1000px",
      backgroundImage:
        this.props.identity === 99 ? `url("${background}")` : null,
      backgroundRepeat: elongate > 5 ? "repeat" : "noRepeat",
      backgroundSize: "cover",
      position: "relative"
    };

    const delBruttoStyle = {
      height: "150px",
      width: "1000px",
      position: "relative",
      backgroundColor:
        this.props.identity === 81
          ? "#cc0000"
          : this.props.identity === 82
          ? "#ff0000"
          : this.props.identity === 83
          ? "#ffad99"
          : null
    };
    if (this.props.identity === 99) {
      const zone99End = this.props.passed_data.data
        .filter((item) => item.zone === 99)
        .map((i) => (
          <Argument key={i.id} item={i} handleChange={this.handleChange} />
        ));
      elongate = zone99End.length;
    }

    const ask =
      this.props.identity === 99 && this.props.fill.length < 1
        ? "Drag clues into this virtual notebook"
        : this.props.identity > 50 &&
          this.props.identity !== 99 &&
          this.props.identity !== 81 &&
          this.props.identity !== 82 &&
          this.props.identity !== 83 &&
          this.props.fill.length < 1
        ? "Drag clues onto here to justify your decision and unlock the option below"
        : null;

    return (
      <div
        className={"drag-drop-zone"}
        identity={this.props.identity}
        onDrop={(e) => this.props.handleDrop(e, this.props.identity)}
        onDragOver={(e) => this.handleDragOver(e)}
        onDragEnter={(e) => this.handleDragEnter(e)}
        onDragLeave={(e) => this.handleDragLeave(e)}
        style={this.props.identity > 80 && this.props.identity < 90 ? delBruttoStyle : normalStyle}
      >
        <h3
          style={{
            textAlign: "center",
            paddingTop: "13px",
            fontFamily: "Chalkduster",
            fontSize: "30px"
          }}
        >
          {noteb}
        </h3>
        <div style={{ paddingLeft: "80px" }}>{this.props.fill}</div>
        <img
          src={
            this.props.identity === 1000
              ? cdiag
              : this.props.identity === 2
              ? cbody
              : this.props.identity === 1
              ? ccomputer
              : this.props.identity >= 101
              ? cpostit
              : null
          }
          alt=" "
          style={{
            width: "180px",
            position: "absolute",
            bottom: "0",
            right: "0"
          }}
        />
        <p
          style={{
            color: "grey",
            textAlign: "center",
            lineHeight: "255px",
            fontSize: "25px",
            textShadow: "2px 2px white"
          }}
        >
          {" "}
          {ask}{" "}
        </p>
      </div>
    );
  }
}
export default Board;
