import React from "react";
// import Argument from "./Argument.js";
import "./styles.css";
import imageCysts from "./data/mriv2_data.js";
import annotated from "./pics/CCbrain_annotated.png";

class Feedback extends React.Component {
  render() {
    const zone1000End = this.props.passed_data.data
      .filter((item) => item.zone === 1000)
      .map((i) => <p key={i.id}> {i.text} </p>);

      const asked = this.props.passed_data.data
      .filter((item) => item.hidden === false)
      .map((i) => <p key={i.id}> {i.about} </p>);

    const imagingFeedback = this.props.passed_data.sups.map((i) =>
      i.type === i.foundAs ? (
        <p key={i.id} style={{ color: "green" }}>
          {i.id + 1}: {i.type} was correctly identified.
        </p>
      ) : i.foundAs === "" ? (
        <p key={i.id} style={{ color: "grey" }}>
          {i.id + 1}: {i.type}. (You didn't identify this)
        </p>
      ) : (
        <p key={i.id} style={{ color: "red" }}>
          {i.id + 1}: {i.type}. (You erroneously identified it as a {i.foundAs})
        </p>
      )
    );

    return (
      <div style={{ backgroundColor: "black" }}>
        <div >
          <p style={{ color: "white" }}>
            You diagnosed: {this.props.passed_data.diag}, because
          </p>
          <h6 style={{ color: "white" }}>{zone1000End}</h6>
          <br />
          <h2 style={{ color: "white" }}>You asked about : {asked}</h2>
          <br />
          <h2 style={{ color: "white" }}>Brain imaging</h2>
          <h1 style={{ color: "white" }}>
            There were {imageCysts.length} cysts or calcifications to be
            identified.
          </h1>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center"
            }}
          >
            <img
              src={annotated}
              alt="brainImage"
              height="300px"
              width="280px"
              style={{ marginRight: "50px" }}
            />
            <div>{imagingFeedback}</div></div>

            {this.props.passed_data.wrongClicks === 1? <h1 style={{ color: "red" }}> One zone was clicked without
            being neither a cyst nor a calcification.</h1>:this.props.passed_data.wrongClicks > 1? <h1 style={{ color: "red" }}> {this.props.passed_data.wrongClicks} zones were clicked without
            being neither a cyst nor a calcification.</h1>: null}
          

          <br />
          <div style={{ display: "flex", justifyContent: "center" }}>
            <a
              href="https://sannine78.github.io/csb-w23xg/"
              style={{
                padding: "8px",
                color: "white",
                border: "1px solid white",
                textDecoration: "none",
                marginBottom: "100px",
                fontFamily: "Geneva"
              }}
            >
              Next patient
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default Feedback;
