import React from "react";
import Board from "./Board.js";
import Argument from "./Argument";
import Back from "./Back";

class Confirm extends React.Component {
  render() {
    const zone = this.props.passed_data.data
      .filter((item) => item.zone === this.props.passed_data.selectedTestId)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));

    var linkTo = "";
    switch (this.props.passed_data.selectedTestId) {
      case 71:
        linkTo = "Home";
        break;
      case 72:
        linkTo = "BrainImaging";
        break;
      case 73:
        linkTo = "Home";
        break;
      case 74:
        linkTo = "Home";
        break;
      case 75:
        linkTo = "ImmunoblotExercise";
        break;
      default:
        linkTo = "Home";
    }

    return (
      <>
        <Back
          func={
            this.props.passed_data.selectedTestId < 74
              ? () => document.getElementById("ImagingMenu").click()
              : () => document.getElementById("SerologyMenu").click()
          }
        />
        <h1>
          What leads you to think that a $
          {this.props.passed_data.selectedTestPrice} expenditure is necessary
       for a&nbsp;{this.props.passed_data.selectedTestType} for your patient?
        </h1>
        <Board
          handleDrop={this.props.handleDrop}
          identity={this.props.passed_data.selectedTestId}
          fill={zone}
        ></Board>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <button
            id="perform"
            onClick={() => document.getElementById(linkTo).click()}
            style={{
              fontSize: "23px",
              fontFamily: "Geneva",
              padding: "10, 10, 10, 10",
              marginTop: "10px"
            }}
            disabled={zone.length < 1}
          >
            Perform test
          </button>
        </div>
      </>
    );
  }
}

export default Confirm;
