import React from "react";
import "./styles.css";
import Back from "./Back";

class ChoiceMenu extends React.Component {
  render() {
    const usedStyle = {
      fontFamily: "Geneva",
      fontSize: "23px",
      padding: "10, 10, 10, 10",
      marginTop: "10px",
      width: "460px",
      marginLeft: "100px",
      marginRight: "100px"
    };

    const butts = this.props.menuItems.map((i) => (
      <button
        key={i.id}
        id={i.type}
        onClick={() => {
          this.props.func(i.id, i.type, i.price);
          document.getElementById("Confirm").click();
        }}
        style={usedStyle}
        disabled={!i.available}
      >
        {i.type} - ${i.price}
      </button>
    ));

    return (
      <>
        <Back func={() => document.getElementById("Home").click()} />
        <div style={{ height: "450px" }}>
          <h3>
            Which {this.props.type} method is necessary, if any? Order the
            chosen test. Only the following option(s) can be offered, given the
            resource limited setting:
          </h3>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              flexWrap: "wrap"
            }}
          >
            {butts}
          </div>
        </div>
      </>
    );
  }
}

export default ChoiceMenu;
