import React from "react";

import white from "../pics/circleWhite.png";
import yellow from "../pics/circleYellow.png";
import green from "../pics/circleGreen.png";
import red from "../pics/circleRed.png";

class Supposition extends React.Component {
  render() {
    return (
      <>
        <div
          style={{
            position: "absolute",
            top: this.props.x - 10,
            left: this.props.y - 10
          }}
        >
          <img
            alt="O"
            src={
              this.props.passed_data.suppositions[this.props.id].type === ""
                ? white
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Active cyst"
                ? red
                : this.props.passed_data.suppositions[this.props.id].type ===
                  "Calcification"
                ? green
                : yellow
            }
            style={{ width: "20px", height: "20px" }}
          />
        </div>
      </>
    );
  }
}

export default Supposition;
