import React from "react";
import "./styles.css";

function Argument(props) {
  if (props.item.id < 1000 && !props.item.hidden) {
    return (
      <p
        id="draggableArgument"
        draggable="true"
        style={{ fontFamily: "Geneva, Verdana", paddingTop: "0px" }}
        onDragStart={() => props.handleChange(props.item.id, props.item.text)}
      >
        {props.item.text}
        <br />
      </p>
    );
  } else if (props.item.hidden) {
    return (
      <div style={{ display: "flex" }}>
        <button
          style={{
            fontFamily: "Geneva",
            fontSize: "15px",
            padding: "10, 10, 10, 10"
          }}
          onClick={props.handleDisplayArgument}
        >
          Ask about {props.item.about}
        </button>
      </div>
    );
  } else {
    return (
      <p
        id="nondraggableArgument"
        draggable="false"
        style={{
          fontFamily: "Geneva, Verdana",
          paddingTop: "0px",
          textDecoration: "underline"
        }}
      >
        {props.item.text}
        <br />
      </p>
    );
  }
}

export default Argument;
